<template>
<v-col  >
 <!-- {{data}} -->
 


  <v-card v-if="found" flat  >
  <v-card-actions v-if="buttons && buttons.length && !isNew">
    <v-btn v-for="(button, index) in buttons" :key="index" v-bind="button.bind" v-on="button.on(vm)" >
      <v-icon v-if="button.icon" >{{button.icon}}</v-icon>
      {{button.text}}
    </v-btn>
  </v-card-actions>  
  <v-card-title> {{ title }} </v-card-title>
  <v-card-text  >
  
 
  <!-- {{ data }} -->

    <v-tabs v-model="tab" v-if="tabs.length > 1"
     center-active
     >
      <v-tab v-for=" (tab, index)  in tabs" :key="index">
        {{tab}}  
      </v-tab>
    </v-tabs>
    <v-form ref="form" @submit="validate">
    <v-tabs-items v-model="tab"  
     center-active
      v-if="!loading"
     >
      <v-tab-item v-for=" (tab, index)  in tabs" :key="index" class="px-5">
        
      <v-row v-for="(field, ind) in filteredFields" :key="ind"   >
 
   
     <v-skeleton-loader
          v-if="loading"
          :loading="loading"
          width="100%"
          height="50"
          type="table-heading"
        >
        </v-skeleton-loader>
        <component 
          :disabled="saveLoading"
          :is="inputs[field.input]" 
          v-bind="field.bind"
          v-on="field.on"
          v-model="data[field.name]" 
          :ref="`${$route.params.collection + '_' + field.name}`" />
      </v-row>

      </v-tab-item>
    </v-tabs-items> 
    </v-form>

  </v-card-text>
 


  </v-card >
  
  <v-not-found v-else/>
  <br/>
  <v-toolbar class="bottom-toolbar"  width="100%" >
    <v-btn
      color="primary"
      class="mr-4"
      @click="validate"
      v-if="!isNew && updateAccess"
      :disabled="saveLoading"
      :loading="saveLoading"
    >
     Update
    </v-btn>
    <v-btn
      color="primary"
      class="mr-4"
      @click="validate"
      v-if="isNew && writeAccess"
      :disabled="saveLoading"
      :loading="saveLoading"
    >
     Save
    </v-btn>
    <!-- <v-btn
      v-if="isNew"
      color="success"
      class="mr-4"
      @click="validate($event, true)"
    >
     Save and new

    </v-btn> -->
     <v-spacer />
     <v-btn v-if="!isNew && $route.params.collection !== 'user' && deleteAccess"
      color="red"
      class="mr-4 white--text"
      @click="removeItem"
    >
      Delete
    </v-btn> 
    <v-btn
      :disabled="saveLoading"
      class="mr-4"
      @click="$router.go(-1)"
    >
      Cancel
    </v-btn>
   
     
  </v-toolbar>
</v-col>
</template>

<script>
import   {
  VTextField,
  VSelect,
  VSwitch,
  
} from 'vuetify/lib'

import AppAutoComplete from './../components/Autocomplete'
import AppPassword from './../components/Password'
import ProfileImage from './../components/ProfileImage'
import AppDatePicker from './../components/DatePicker'
import AppIconInput from './../components/IconInput'
import AppMenuItemInput from './../components/MenuItemInput'
import Rights from './../components/Rights'
import Assignment from './../components/Assignment'

import { mapActions } from 'vuex'
 
import collections from './../collections'
 export default {
   name: 'Item',
  
   data () {
     return {
       showFields: [],
       loading: false,
       saveLoading: false,
       collection: null,
       tab: 0,
       form: null,
       found: true,
       title: '',
       data: {},
       inputs: {
         text: VTextField,
         select: VSelect,
         switch: VSwitch,
         date: AppDatePicker,
         autocomplete: AppAutoComplete,
         password: AppPassword,
         rights: Rights,
         icon: AppIconInput,
         menu_item: AppMenuItemInput,
         assignment: Assignment,
         'profile-image': ProfileImage
       }
     }
   },
   watch: {
     '$route':{
       handler:'updateComponent',
       immediate:true
     }
   },
   computed: {
     vm () {
       return this
     },
     buttons () {
       const collection = collections.get(this.$route.params.collection)
       if (collection) {
         return collection.buttons
       }
       return false
     },
     filteredFields () {
       
       return this.fields.filter((f,i) => this.showFields[this.tab][i])
     },
     tabs () {
       return this.form ? this.form.filter(tab => {
        if (typeof tab.if == 'function')
          return tab.if(this)
        else if (typeof tab.if == 'boolean')
          return tab.if
        else
          return true
       }).map(e => e.label) : []
     },
     fields () {
       return this.form ? this.form[this.tab].fields : []
     },
     isNew () {
       return !this.$route.params.id
     },
     access () {
        if (this.$store.state.auth.user.rights) {
          return this.$store.state.auth.user.rights.filter(e => {
              return e.collection == this.$route.params.collection  
          })
        } 
        return []
      },
      updateAccess () {
      
        if (this.access.length > 0)
          return this.access[0].update.value

        return false 
      },
      writeAccess () {
      
        if (this.access.length > 0)
          return this.access[0].write.value

        return false 
      },
      deleteAccess () {
         if (this.access.length > 0)
          return this.access[0].delete.value

        return false 
      },
   },
   methods: {
     ...mapActions({
       getItem: 'api/getById',
       save: 'api/save',
       remove: 'api/remove',
       update: 'api/update',
       success: 'success'

     }),
     async removeItem () {
        const res = await this.$confirm('Are you sure do you want to delete this item ?', { title: 'Warning' })
        if (res) {
            await this.remove({ collection: this.$route.params.collection, id: this.$route.params.id })
            this.$router.go(-1)
            this.$store.commit('success', this.title +' deleted successfuly')
        }
        
     },
     generateDataInputs (collection, values) {
       const data = {}

       collection.forEach((e,ti) => {
          this.showFields[ti] = []
          e.fields.forEach((field, fi) => {
             
            if( typeof field.if == 'function') {
              this.showFields[ti][fi] = field.if(this.$store.state)
            } else
              this.showFields[ti][fi] = true


           if (field.name == 'password') return data[field.name] = ''
             
           if (field.input == 'date') { 
             if (values[field.name]) {
               values[field.name] =  values[field.name].split('T')[0] 
             }
           }

      

          data[field.name] = ''
          if (values[field.name]) {
            data[field.name] = values[field.name]
          } else if(field.default) {
            data[field.name] = typeof field.default == "function" ? field.default(this.$store.state) : field.default
          }

            // data[field.name] = values[field.name] ? values[field.name] : (field.default || '')
            
          })
       })
   
       this.data = data
       
     },
     async validate (evt, saveAndNew = false) {
       evt.preventDefault()
       this.saveLoading = true
        const { data,  collection, title} = this
        if(this.$refs.form.validate()){
         
          
          const res =  this.isNew ? await this.save({ collection, data }) : await this.update({ collection, data, id: this.$route.params.id })
        
          if (res) {
            this.$store.commit('success', title + (!this.isNew ? ' updated' :' saved') +' successfuly')
             if (saveAndNew) {
              if (this.isNew){
                  this.generateDataInputs(this.form, {})
                  this.$refs.form.resetValidation()
              }
                
              else
                this.$router.push('/'+this.$route.params.collection+'/new')

              
            }
            else {
              // if (this.$route.query.backMode)
                this.$router.go(-1);
              // else
                // this.$router.replace('/'+this.$route.params.collection+'/'+res.data._id);
            }
          }
        
         
        } else {
          this.$store.commit('error', 'form not valid')
        }
       this.saveLoading = false
         
      },
     async updateComponent () {
        const collection = collections.get(this.$route.params.collection)
        this.collection = this.$route.params.collection
        if (collection) {
          this.title = collection.title
          this.form = collection.form
          let data = {}
          if(this.$route.params.id || this.$route.query.clone) {
            const id = this.$route.params.id || this.$route.query.clone
            
            this.loading = true
            const res = await this.getItem({
              collection: this.$route.params.collection,
              id 
            })
            const { fields } = this.$route.query
            
            if (fields) {
              if (Array.isArray(fields)) {
                fields.forEach(field => {
                data[field] = res.data[field]

                })
              } else {
                data[fields] = res.data[fields]
              }
            } else
              data = res.data
            this.loading = false

          }

          
          // append values to fields if match query
          if(this.isNew) {
            const keys = Object.keys(this.$route.query)
            keys.forEach(key => {
              const field = this.fields.filter(e => e.name === key)
              if (field.length) {
                data[key] = this.$route.query[key]
              }
            })
          }
          
          this.generateDataInputs(this.form, data)
        } else 
          this.found = false
     }  
   },
   mounted () {
     // access from tables.js
      window.$form = this 
   },
   destroyed () {
     window.$form = null
   } 

 }
</script>
<style >
.bottom-toolbar {
  position: absolute !important;
  bottom: 0;
  left: 0;
}
</style>